/* src/Login.css */

.login-background {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-image: url('https://i.ibb.co/5hfmsS1/Design-sem-nome.png'); /* Link da imagem */
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  
  .login-container {
    background-color: rgba(255, 255, 255, 0.85); /* Transparência no container para melhor contraste */
    padding: 30px;
    border-radius: 20px;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .login-avatar {
    margin: 0 auto;
    background-color: #03d815;
    width: 80px;
    height: 80px;
  }
  
  .login-title {
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    margin-top: 20px;
    color: #333;
  }
  
  .login-input {
    margin-top: 20px;
    margin-bottom: 10px;
  }
  
/* src/Login.css */

.login-button {
    margin-top: 20px;
    background-color: #01d636;
    padding: 10px 0;
    font-size: 16px;
    transition: all 0.3s ease-in-out;
    animation: pulse 2s infinite;
  }
  
  .login-button:hover {
    background-color: #28bb0b;
    transition: background-color 0.3s ease; /* Suave transição de cor */
  }
  
  
  .login-error {
    margin-top: 10px;
    font-size: 14px;
    font-weight: bold;
  }
  
  /* src/Login.css */
@keyframes fadeInSlideDown {
    0% {
      opacity: 0;
      transform: translateY(-50px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .login-title {
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    margin-top: 20px;
    color: #333;
    animation: fadeInSlideDown 1s ease-in-out; /* Aplicando a animação */
  }
  
@keyframes pulse {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.05); /* Aumenta um pouco o tamanho */
    }
    100% {
      transform: scale(1);
    }
  }